<template>
  <view class="create-task-page">
    <view class="form-card">
      <view class="form-item">
        <text class="label">任务标题</text>
        <input class="input" v-model="form.title" placeholder="例如：本周5公里耐力跑" />
      </view>
      
      <view class="form-item">
        <text class="label">任务类型</text>
        <picker mode="selector" :range="taskTypes" @change="onTypeChange">
          <view class="picker-box">
            <text>{{ form.type || '请选择任务类型' }}</text>
            <text class="arrow">></text>
          </view>
        </picker>
      </view>

      <view class="form-item">
        <text class="label">目标距离 (km)</text>
        <input class="input" type="digit" v-model="form.distance" placeholder="0.0" />
      </view>
      
      <view class="form-item">
        <text class="label">截止日期</text>
        <picker mode="date" @change="onDateChange">
          <view class="picker-box">
            <text>{{ form.deadline || '请选择截止日期' }}</text>
            <text class="arrow">></text>
          </view>
        </picker>
      </view>

      <view class="form-item">
        <text class="label">指派对象</text>
        <picker mode="selector" :range="groupOptions" @change="onGroupChange">
          <view class="picker-box">
            <text>{{ form.target || '全员' }}</text>
            <text class="arrow">></text>
          </view>
        </picker>
      </view>

      <view class="form-item vertical">
        <text class="label">任务说明</text>
        <textarea class="textarea" v-model="form.description" placeholder="请输入任务的具体要求和注意事项..." />
      </view>
    </view>

    <view class="footer-btn">
      <button class="submit-btn" @click="submitTask">发布任务</button>
    </view>
  </view>
</template>

<script setup>
import { ref } from 'vue';

const taskTypes = ['普通跑步', '配速跑', '间歇跑', '体能测试'];
const groupOptions = ['全员', '体能A组', '体能B组', '康复组'];

const form = ref({
  title: '',
  type: '',
  distance: '',
  deadline: '',
  target: '全员',
  description: ''
});

const onTypeChange = (e) => {
  form.value.type = taskTypes[e.detail.value];
};

const onDateChange = (e) => {
  form.value.deadline = e.detail.value;
};

const onGroupChange = (e) => {
  form.value.target = groupOptions[e.detail.value];
};

const submitTask = () => {
  if (!form.value.title || !form.value.type || !form.value.deadline) {
    return uni.showToast({ title: '请完善任务信息', icon: 'none' });
  }
  
  uni.showLoading({ title: '发布中...' });
  setTimeout(() => {
    uni.hideLoading();
    uni.showToast({ title: '任务发布成功', icon: 'success' });
    
    // Simulate adding task (In real app, post to API)
    const newTask = {
      id: 'T' + Date.now(),
      title: form.value.title,
      type: form.value.type,
      deadline: form.value.deadline,
      progress: 0,
      total: 128, // Mock total
      completed: 0
    };
    
    // Pass back to previous page via event channel or global state if needed
    // For now just go back
    setTimeout(() => {
      uni.navigateBack();
    }, 1500);
  }, 1000);
};
</script>

